import React from "react"
import ReactDOMClient from "react-dom/client"
import './menue2.css'
import { show } from '../userpage.js'
import { userSupport } from '../usersupport.js'
import { userTreaty } from '../usertreaty.js'
import { exit, getMess, getSup } from '../userpage.js'
import { k } from '../tariffs'
import { SetLogin, SetReg, menue, MainPage } from '../index.js'
import { controlSchedule } from '../schedule'
import { sysStat, sysStatClient } from '../sysstats'
import { VideoChat } from '../videochat'
import { chtariffset } from '../changetariff'
import { ctrlAcc } from '../controlaccess'

const Menue2 = ({ header, items, active, setActive, g }) => {
    if (g === "1") {
        return (
            <div className={active ? 'menue2 active' : 'menue2'} onClick={() => setActive(false)}>
                <div className="blur" />
                <div className="menue_context" onClick={e => e.stopPropagation()}>
                    <div className="menue_header">{header}</div>
                    <ul>
                        <li>
                            <p onClick={show}> </p>
                            <span className="material-icons"></span>
                        </li>

                        <li>
                            <p onClick={k}></p>
                            <span className="material-icons"></span>
                        </li>

                        <li>
                            <p onClick={userSupport}></p>
                            <span className="material-icons"></span>
                        </li>

                        <li>
                            <p onClick={userTreaty}> </p>
                            <span className="material-icons"></span>
                        </li>

                        <li>
                            <p onClick={exit}></p>
                            <span className="material-icons"></span>
                        </li>
                    </ul>
                </div>
            </div>
        )
    }

    if (g === "2") {
        return (
            <div className={active ? 'menue2 active' : 'menue2'} onClick={() => setActive(false)}>
                <div className="blur" />
                <div className="menue_context" onClick={e => e.stopPropagation()}>
                    <div className="menue_header">{header}</div>
                    <ul>
                        <li>
                            <p onClick={k}></p>
                            <span className="material-icons"></span>
                        </li>

                        <li>
                            <p onClick={SetLogin}></p>
                            <span className="material-icons"></span>
                        </li>

                        <li>
                            <p onClick={SetReg}></p>
                            <span className="material-icons"></span>
                        </li>
                    </ul>
                </div>
            </div>
        )
    }

    if (g === "3") {
        return (
            <div className={active ? 'menue2 active' : 'menue2'} onClick={() => setActive(false)}>
                <div className="blur" />
                <div className="menue_context" onClick={e => e.stopPropagation()}>
                    <div className="menue_header">{header}</div>
                    <ul>
                        <li>
                            <p><a href="/"></a></p>
                            <span className="material-icons"></span>
                        </li>

                        <li>
                            <p onClick={SetLogin}></p>
                            <span className="material-icons"></span>
                        </li>

                        <li>
                            <p onClick={SetReg}></p>
                            <span className="material-icons"></span>
                        </li>
                    </ul>
                </div>
            </div>
        )
    }

    if (g === "4") {
        return (
            <div className={active ? 'menue2 active' : 'menue2'} onClick={() => setActive(false)}>
                <div className="blur" />
                <div className="menue_context" onClick={e => e.stopPropagation()}>
                    <div className="menue_header">{header}</div>
                    <ul>
                        <li>
                            <p onClick={chtariffset}> </p>
                            <span className="material-icons"></span>
                        </li>

                        <li>
                            <p onClick={ctrlAcc}>  </p>
                            <span className="material-icons"></span>
                        </li>

                        <li>
                            <p onClick={VideoChat}> </p>
                            <span className="material-icons"></span>
                        </li>

                        <li>
                            <p onClick={controlSchedule}> </p>
                            <span className="material-icons"></span>
                        </li>
                        <li>
                            <p onClick={sysStatClient}></p>
                            <span className="material-icons"></span>
                        </li>
                        <li>
                            <p onClick={exit}></p>
                            <span className="material-icons"></span>
                        </li>
                    </ul>
                </div>
            </div>
        )
    }

    if (g === "5") {
        return (
            <div className={active ? 'menue2 active' : 'menue2'} onClick={() => setActive(false)}>
                <div className="blur" />
                <div className="menue_context" onClick={e => e.stopPropagation()}>
                    <div className="menue_header">{header}</div>
                    <ul>
                        <li>
                            <p onClick={show}> </p>
                            <span className="material-icons"></span>
                        </li>

                        <li>
                            <p onClick={VideoChat}> </p>
                            <span className="material-icons"></span>
                        </li>

                        <li>
                            <p onClick={controlSchedule}> </p>
                            <span className="material-icons"></span>
                        </li>

                        <li>
                            <p onClick={sysStatClient}></p>
                            <span className="material-icons"></span>
                        </li>

                        <li>
                            <p onClick={getMess}></p>
                            <span className="material-icons"></span>
                        </li>

                        <li>
                            <p onClick={exit}></p>
                            <span className="material-icons"></span>
                        </li>
                    </ul>
                </div>
            </div>
        )
    }

    if (g === "6") {
        return (
            <div className={active ? 'menue2 active' : 'menue2'} onClick={() => setActive(false)}>
                <div className="blur" />
                <div className="menue_context" onClick={e => e.stopPropagation()}>
                    <div className="menue_header">{header}</div>
                    <ul>
                        <li>
                            <p onClick={VideoChat}> </p>
                            <span className="material-icons"></span>
                        </li>

                        <li>
                            <p onClick={controlSchedule}> </p>
                            <span className="material-icons"></span>
                        </li>

                        <li>
                            <p onClick={getSup}></p>
                            <span className="material-icons"></span>
                        </li>

                        <li>
                            <p onClick={exit}></p>
                            <span className="material-icons"></span>
                        </li>
                    </ul>
                </div>
            </div>
        )
    }
}

export default Menue2;