import React from "react"
import ReactDOMClient from "react-dom/client"

function SysStat() {
    return (
        <div id="sysStat">
            <p>   </p>

            <p>  : <input type="date" name="calendar"></input></p>
            <p>  : <input type="date" name="calendar"></input></p>

            <textarea placeholder="" id="nameTariffch"></textarea>
            <button></button>
        </div>
    )
}

function SysStatClient() {
    return (
        <div id="sysStat">
            <p> </p>

            <p>  : <input type="date" name="calendar"></input></p>
            <p>  : <input type="date" name="calendar"></input></p>

            <button></button>
            <button>  </button>
            <button> </button>

            <textarea placeholder="" id="nameTariffch"></textarea>
            <p></p>
            <button></button>
        </div>
    )
}

export function sysStat() {
    const fullinfo = ReactDOMClient.createRoot(document.getElementById("fullinfo"));
    fullinfo.render(<SysStat />);
}

export function sysStatClient() {
    const fullinfo = ReactDOMClient.createRoot(document.getElementById("fullinfo"));
    fullinfo.render(<SysStatClient />);
}